.a-float1 {
  animation: bubble 5s linear infinite;
  animation-direction: alternate;
}

.a-float2 {
  animation: bubble-reverse2 5s linear infinite;
  animation-direction: alternate;
}

.a-float3 {
  animation: bubble-reverse 5s linear infinite;
  animation-direction: alternate;
}

.a-scale {
  animation: scale 1s infinite ease-in-out alternate;
}

.a-zoomInLightShow {
  opacity: 0;
  animation: scaleImg-show 1.5s linear alternate;
}

.a-zoomInLightHide {
  animation: scaleImg-hide 1.5s linear forwards;
}

.a-float {
  animation: float 2s linear infinite;
  animation-direction: alternate;
}

.a-float-reverse {
  animation: float 2s linear infinite;
  animation-direction: alternate-reverse;
}

@keyframes img_shine {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes scale {
  from {
    transform: scale(0.95);
  }
  to {
    transform: scale(1);
  }
}

@keyframes scaleImg-show {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  to {
    opacity: 1;
    transform: scale(1.5);
  }
}

@keyframes scaleImg-hide {
  0% {
    opacity: 1;
    transform: scale(1.5);
  }
  to {
    opacity: 0;
    transform: scale(1);
  }
}

@keyframes float {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, 10px);
  }
}

@keyframes bubble {
  0% {
    transform: translate(3px, 5px);
  }
  33% {
    transform: translate(0px, 8px);
  }
  66% {
    transform: translate(-3px, 5px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

@keyframes bubble-reverse {
  0% {
    transform: translate(-5px, 8px);
  }
  33% {
    transform: translate(0px, 12px);
  }
  66% {
    transform: translate(-5px, 15px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

@keyframes bubble-reverse2 {
  0% {
    transform: translate(5px, 10px);
  }
  33% {
    transform: translate(3px, 18px);
  }
  66% {
    transform: translate(-5px, 13px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

.e-btn {
  /* Structure */
  display: inline-block;
  zoom: 1;
  line-height: normal;
  white-space: nowrap;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  box-sizing: border-box;
}

/* Firefox: Get rid of the inner focus border */
.e-btn::-moz-focus-inner {
  padding: 0;
  border: 0;
}

/* Inherit .pure-g styles */
.e-btn-group {
  letter-spacing: -0.31em;
  /* Webkit: collapse white-space between units */
  *letter-spacing: normal;
  /* reset IE < 8 */
  *word-spacing: -0.43em;
  /* IE < 8: collapse white-space between units */
  text-rendering: optimizespeed;
  /* Webkit: fixes text-rendering: optimizeLegibility */
}

.opera-only :-o-prefocus,
.e-btn-group {
  word-spacing: -0.43em;
}

.e-btn-group .e-btn {
  letter-spacing: normal;
  word-spacing: normal;
  vertical-align: top;
  text-rendering: auto;
}

